.sign-in_title {
  @include bigger-main-text;

  margin-bottom: 30px;

  font-size: 18px;
  text-align: center;
}

.modal-close {
  position: absolute;
  top: 20px;
  right: 20px;

  width: 19px;
  height: 30px;

  background-color: transparent;
  background-image: url("../img/close.png");
  background-repeat: no-repeat;
  border: none;

  &:hover {
    background-image: url("../img/close-hover.png");
  }
}

.sign-in {
  position: relative;

  display: block;
  width: 360px;
  margin: auto;
  padding: 30px;

  border: 1px solid $special-light-grey;
  border-radius: 10px;
  box-shadow: 0 0 20px 20px rgba(0, 0, 0, 0.1);
}

.sign-in_input {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px 20px;

  border: 1px solid $special-grey;
  border-radius: 10px;
  background-color: transparent;
  color: $basic-grey;

  &:focus {
    background-color: $basic-light-grey;
    outline: none;
  }

  &::placeholder {
    color: $basic-grey;
  }
}

.sign-in_button {
  display: block;
  width: 140px;
  height: 50px;
  margin: 0 auto;

  color: $basic-white;

  background-color: $basic-blue;
  border: none;
  border-radius: 10px;

  &:hover {
    opacity: 0.7;
  }
}

.sign-in_form div {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.sign-up_form div {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.forget-password {
  @include blue-text;

  color: $basic-grey;
  text-decoration: underline;

  &:hover {
    color: $special-grey;
  }
}

.sign-in-link {
  @include blue-text;

  text-decoration: underline;

  &:hover {
    color: $special-grey;
  }
}

.modal {
  position: fixed;
  top: 10%;
  right: 0;
  left: 0;

  display: none;
  margin: auto;
}

.modal-show {
  display: block;
}
