@mixin reset-list {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin main-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: $basic-grey;
}

@mixin small-text {
  font-weight: 500;
  font-size: 14px;
  color: $basic-black;
}

@mixin medium-text {
  font-weight: 500;
  font-size: 16px;
  color: $basic-black;
}

@mixin bigger-main-text {
  font-weight: 800;
  font-size: 30px;
  color: $basic-black;
}

@mixin blue-text {
  font-weight: 400;
  font-size: 14px;
  color: $basic-blue;
}

@mixin medium-header {
  font-weight: 500;
  font-size: 35px;
  color: $basic-black;
}

@mixin small-header {
  font-weight: 800;
  font-size: 25px;
  color: $basic-black;
}
