.page-header {
  background-image: url("../img/bg-menu.jpg");
  background-repeat: no-repeat;
}

.page-header_nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.site-menu_list {
  @include reset-list;

  display: flex;
  flex-direction: row;
}


.site-menu_item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

.page-header_img {
  &:hover {
    opacity: 0.7;
  }
}

.site-menu_link {
  @include main-text;

  text-decoration: none;

  cursor: pointer;

  &:hover {
    color: $basic-blue;
  }
}

.user-nav {
  @include reset-list;

  display: flex;
  justify-content: space-between;
}


.user-nav_link {
  @include medium-text;

  padding-left: 20px;

  text-decoration: none;

  cursor: pointer;

  &:hover {
    color: $basic-blue;
  }
}

.user-nav_link--sign-in {
  margin-right: 30px;

  background-image: url("../img/user.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.user-nav_link--sign-up {
  padding-left: 30px;

  background-image: url("../img/user-in.png");
  background-repeat: no-repeat;
  background-position: left center;
}
