.form {
  margin-top: 40px;
}

.country-select {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.country-select_item {
  width: 192px;
  margin-bottom: 20px;
  padding: 8px 0 7px 12px;

  border: 1px solid $special-grey;
  border-radius: 10px;
  cursor: pointer;

  &:nth-child(odd) {
    margin-right: 20px;
  }
}

.country-select_item-label {
  @include small-text;

  cursor: pointer;

  & span {
    color: $basic-grey;
  }
}

.country-select_item-input {
  background-color: transparent;
}

.country-select_item:hover {
  opacity: 0.6;
}

.country-select_item:focus {
  background-color: $basic-light-grey;
  outline: none;
}

.country-select_item-input + .country-select_item-label {
  border: 1px solid $basic-black;
}

.country-select_item-input:disabled + .country-select_item-label {
  cursor: auto;
  opacity: 0.3;
}

.operator {
  margin-top: 30px;
}

.operator_list {
  width: 403px;
  height: 50px;
  padding-left: 19px;

  background-image: url("../img/arrow.png");
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: 370px center;
  border: 1px solid $special-grey;
  border-radius: 10px;
  cursor: pointer;

  webkit-appearance: none;
  moz-appearance: none;
  appearance: none;

  @include main-text;

  &:hover {
    opacity: 0.5;
  }

  &:focus {
    background-color: $basic-light-grey;
    outline: none;
  }
}

.operator_list::-ms-expand {
  display: none;
}

.operator_list-item {
  padding: 10px;

  cursor: pointer;

  @include main-text;
}

.operator_list-item:checked {
  color: $basic-white;

  background-color: $basic-yellow;
}

.services {
  @include reset-list;

  margin-top: 40px;
}

.selected {
  position: relative;

  width: 200px;
  margin-bottom: 25px;
}

.selected_label {
  position: relative;

  margin-left: 36px;

  cursor: pointer;
}

.selected_label::before {
  content: "";
  position: absolute;
  left: -36px;

  width: 24px;
  height: 24px;

  border: 1px solid $special-grey;
  border-radius: 5px;
  cursor: pointer;
}

.selected_label:hover::before {
  border: 1px solid $basic-blue;
}

.selected:focus .selected_label::before {
  background-color: $basic-light-grey;
  border-radius: 5px;
  outline: none;
}

.selected:focus {
  outline: none;
}

.selected_input:checked + .selected_label::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -31px;

  width: 16px;
  height: 14px;

  background-image: url("../img/check.png");
  background-repeat: no-repeat;
  cursor: pointer;
}


.services_item_label {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  & div {
    display: flex;
    align-items: center;
  }
}

.services_item {
  width: 400px;
  margin-bottom: 20px;
  padding: 10px 20px;

  border: 1px solid $special-grey;
  border-radius: 10px;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    background-color: $basic-light-grey;
    outline: none;
  }
}

.services_item_label-img {
  margin-left: 20px;
}

.services_item_label-header {
  margin-bottom: 0;
  margin-left: 10px;
}

.services_item_label-price {
  margin-bottom: 0;
  margin-left: 30px;

  @include main-text;

  & span {
    color: $basic-blue;
  }
}

.star-checked {
  width: 20px;
  height: 20px;

  background-image: url("../img/star-selected.png");
  cursor: pointer;
}

.star-unchecked {
  width: 20px;
  height: 20px;

  background-image: url("../img/star-unselected.png");
  cursor: pointer;
}

.services_item-unchecked {
  width: 13px;
  height: 14px;
  margin-left: 10px;

  background-image: url("../img/unchecked.png");
  cursor: pointer;
}

.services_item-checked {
  width: 13px;
  height: 14px;
  margin-left: 10px;

  background-image: url("../img/checked.png");
  cursor: pointer;
}
