.about-us_title {
  @include bigger-main-text;

  margin-top: 40px;
}

.about-us_subtitle {
  @include main-text;

  margin-top: 30px;

  line-height: 30px;
}

.news {
  margin-top: 60px;
}

.news_title {
  @include bigger-main-text;
}

.news_list {
  @include reset-list;

  margin-top: 30px;
}

.news_list-item {
  margin-bottom: 40px;
  padding: 13px 24px;

  border: 1px solid $special-grey;
  border-radius: 20px;
}

.news_list-item-data {
  @include blue-text;

  padding-left: 15px;

  background-image: url("../img/calendar.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.news_list-item-header {
  @include medium-text;

  font-size: 18px;
}

.news_list-item-description {
  @include main-text;

  margin-top: 30px;

  line-height: 30px;
}
